******************************************************************/
@use '../global/' as *;

.wpcf7-spinner{
  display: none;
}


// 
.wpcf7-response-output{
  text-align: center;
}

// 
.wpcf7-not-valid-tip{
  font-size: rem(14);
  color: $coral;

  @include mq('tab'){
    font-size: rem(16);
  }
}

// 
.wpcf7 form.invalid .wpcf7-response-output, .wpcf7 form.unaccepted .wpcf7-response-output, .wpcf7 form.payment-required .wpcf7-response-output{
  font-size: rem(14);
  color: $text-color;
  border-color: $coral;

  @include mq('tab'){
    font-size: rem(16);
  }
}

// 
.wpcf7 form.sent .wpcf7-response-output{
  font-size: rem(14);
  color: $text-color;
  border-color: $coral;

  @include mq('tab'){
    font-size: rem(16);
  }
}

// 
.wpcf7-validates-as-required.wpcf7-not-valid{
  background-color: rgba(255, 79, 79, .1);;
}