/******************************************************************

.c-btn-hamburger

******************************************************************/
@use '../global/' as *;

.c-btn-hamburger{
  width: rem(35);
  height: rem(15);
  cursor: none;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

// span 2
.c-btn-hamburger span{
  width: 100%;
  height: rem(2);
  //mix-blend-mode
  background-color: $white;
  border-radius: rem(9999);

  transition: all .6s;
}

.c-btn-hamburger span:nth-of-type(1){
  transform: translateY(rem(-5));

  @include animation(
    $name: closeTop,
    $duration: .6s,
  )
}

.c-btn-hamburger span:nth-of-type(2){
  transform: translateY(rem(5));

  @include animation(
    $name: closeBottom,
    $duration: .6s,
  )
}


// is-open
// miz-blend-modeis-openbackground-color
// span 
.c-btn-hamburger.is-open span:nth-of-type(1){

  @include animation(
    $name:  openTop,
    $duration: .6s,
  )
}

.c-btn-hamburger.is-open span:nth-of-type(2){

  @include animation(
    $name: openBottom,
    $duration: .6s,
  )
}

