/***************************************************************

rem()

***************************************************************/
// @function rem($pixels) {
//   @return $pixels / 16 * 1rem;
// }

@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}

/***************************************************************

vw
reset.scss
$window_widthinner
$window_width1000px$size1616vw

***************************************************************/
// @function vw($window_width, $size) {
// 	@return $size / $window_width * 100vw;
// }

@function vw($window_width, $size) {
	@return math.div($size, $window_width) * 100vw;
}


