/******************************************************************

.p-cards-list-03  3

******************************************************************/
@use '../global/' as *;

.p-cards-list-03{
  @include mq('tab'){
    display: flex;
    flex-wrap: wrap;
  }
}

.p-cards-list-03__card{
  @include mq('tab'){
    width: calc(50% - 20px / 2);
  }
  @include mq('pc'){
    width: calc(100% / 3 - 20px * 2 / 3);
  }
}

// margin-top
.p-cards-list-03__card:not(:first-child){
  margin-top: rem(50);

  @include mq('tab'){
    margin-top: 0;
  }
}

// ()margin-right
.p-cards-list-03__card:nth-child(odd){

  @include mq('tab'){
    margin-right: rem(20);
  }
  @include mq('pc'){
    margin-right: 0;
  }
}

//3margin-top
.p-cards-list-03__card:nth-child(n+3){ //n+3...-n+3...3
  @include mq('tab'){
    margin-top: rem(40);
  }
  @include mq('pc'){
    margin-top: 0;
  }
}

//2margin-right
.p-cards-list-03__card:nth-child(3n+1),
.p-cards-list-03__card:nth-child(3n+2){
  @include mq('pc'){
    margin-right: rem(20);
  }
}

//4margin-top
.p-cards-list-03__card:nth-child(n+4){
  @include mq('pc'){
    margin-top: rem(40);
  }
}
