/******************************************************************

.keyframes keyframes

******************************************************************/
@use './functions' as *;

/******************************************************************

******************************************************************/
@keyframes fadeIn {
  0%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

/******************************************************************

******************************************************************/
@keyframes fadeUp {
  0%{
    opacity: 0;
    transform: translateY(20%);
  }
  100%{
    opacity: 1;
    transform: translateY(0);
  }
}

/******************************************************************
// 
******************************************************************/
@keyframes fadeUpMv {
  0%{
    opacity: 0;
    transform: translate(-50%, 5%);
  }
  100%{
    opacity: 1;
    transform: translate(-50%, -50%);
  }
}

/******************************************************************
//  opening
******************************************************************/
@keyframes upFade {
  0%{
    opacity: 1;
    transform: translateY(0);
  }
  100%{
    opacity: 0;
    transform: translateY(-20%);
  }
}

/******************************************************************
 opening
******************************************************************/
@keyframes fade {
  0%{
    opacity: 1;
    visibility: visible;
  }
  100%{
    opacity: 0;
    visibility: hidden;
  }
}

/******************************************************************

******************************************************************/
@keyframes fadeRight {
  0%{
    opacity: 0;
    transform: translateX(rem(20));
  }
  100%{
    opacity: 1;
    transform: translateX(0);
  }
}

/******************************************************************
//   
******************************************************************/
@keyframes emergeUp {
  0%{
    // opacity: 0;
    transform: translateY(1em);
  }
  100%{
    // opacity: 1;
    transform: none;
  }
}

/******************************************************************
 SNS
******************************************************************/
@keyframes emergeUpSns {
  0%{
    transform: translateY(rem(40));
  }
  100%{
    transform: translateY(0);
  }
}

/******************************************************************

******************************************************************/
@keyframes zoomUp {
  0%{
    transform: scale(1);
  }
  100%{
    transform: scale(1.1);
  }
}

/******************************************************************

******************************************************************/
@keyframes downLine {
  0%{
    transform-origin: top;
    transform: scale(1, 0) translateX(-50%);
  }
  50%{
    transform-origin: top;
    transform: scale(1, 1) translateX(-50%);
  }
  50.1%{
    transform-origin: bottom;
    transform: scale(1, 1) translateX(-50%);
  }
  100%{
    transform-origin: bottom;
    transform: scale(1, 0) translateX(-50%);
  }
}

/******************************************************************

******************************************************************/
// 
@keyframes accordion {
  0%{
    opacity: 0;
    transform: translateY(50%);
  }
  100%{
    opacity: 1;
    transform: translateY(0);
  }
}

// 
@keyframes downAccordion {
  0%{
    transform: translateY(0);
    opacity: 1;
  }
  100%{
    transform: translateY(50%);
    opacity: 0;
  }
}

/******************************************************************

******************************************************************/
@keyframes stretchLine {
  0%{
    transform-origin: left;
    transform: scale(0, 1);
  }
  30%{
    transform-origin: left;
    transform: scale(1, 1);
  }
  70%{
    transform-origin: right;
    transform: scale(1, 1);
  }
  100%{
    transform-origin: right;
    transform: scale(0, 1);
  }
}

