/******************************************************************

******************************************************************/
@keyframes scrollDownLine {
  0%{
    opacity: 1;
    transform-origin: top;
    transform: scale(1, 0);
  }
  10%{
    opacity: 1;
    transform-origin: top;
    transform: scale(1, 1);
  }
  90%{
    opacity: 1;
    transform-origin: top;
    transform: scale(1, 1);
  }
  100%{
    opacity: 0;
    transform-origin: top;
    transform: scale(1, 1);
  }
}

/******************************************************************

******************************************************************/
@keyframes circleDown {
  0%{
    transform: translate(-44%, 0) scale(0);
  }
  15%{
    transform: translate(-44%, 0) scale(1);
  }
  85%{
    transform: translate(-44%, rem(96)) scale(1);
  }
  100%{
    transform: translate(-44%, rem(96)) scale(0);
  }
}

/******************************************************************

******************************************************************/
@keyframes downLine {
  0%{
    transform-origin: top;
    transform: scale(1, 0) translateX(-50%);
  }
  50%{
    transform-origin: top;
    transform: scale(1, 1) translateX(-50%);
  }
  50.1%{
    transform-origin: bottom;
    transform: scale(1, 1) translateX(-50%);
  }
  100%{
    transform-origin: bottom;
    transform: scale(1, 0) translateX(-50%);
  }
}


/******************************************************************

******************************************************************/
@keyframes accordionText {
  0%{
    transform: translateY(0);
    // opacity: 1;
  }
  25%{
    transform: translateY(-100%);
    // opacity: 0;
  }
  26%{
    transform: translateY(100%);
    // opacity: 0;
  }
  27%{
    transform: translateY(100%);
    // opacity: 1;
  }
  75%{
    transform: translateY(0);
    // opacity: 1;
  }
  100%{
    transform: translateY(0);
    // opacity: 1;
  }
}

/******************************************************************

******************************************************************/
// 
@keyframes closeTop {
  0%{
    transform: translateY(rem(1)) rotate(43deg);
  }
  50%{
    transform: translateY(rem(1));
  }
  100%{
    transform: translateY(rem(-5));
  }
}

@keyframes openTop {
  0%{
    transform: translateY(rem(-5));
  }
  50%{
    transform: translateY(rem(1));
  }
  100%{
    transform: translateY(rem(1)) rotate(43deg);
  }
}

// 
@keyframes closeBottom {
  0%{
    transform: translateY(rem(-1)) rotate(-43deg);
  }
  50%{
    transform: translateY(rem(-1));
  }
  100%{
    transform: translateY(rem(5));
  }
}

@keyframes openBottom {
  0%{
    transform: translateY(rem(5));
  }
  50%{
    transform: translateY(rem(-1));
  }
  100%{
    transform: translateY(rem(-1)) rotate(-43deg);
  }
}

/******************************************************************
 
******************************************************************/
// 
@keyframes circleToSmall {
  0%{
    transform: scale(1.1);
  }
  100%{
    transform: scale(1);
  }
}
// 
@keyframes circleToLarge {
  0%{
    transform: scale(1);
  }
  100%{
    transform: scale(1.1);
  }
}

/******************************************************************
   
******************************************************************/
@keyframes allowFadeRight {
  0%{
    transform: translate(0, 0);
    opacity: 1;
  }
  40%{
    opacity: 0;
  }
  49.9%{
    transform: translate(1em, 0);
    opacity: 0;
  }
  50%{
    transform: translate(-1em, 0);
    opacity: 0;
  }
  100%{
    transform: translate(0, 0);
    opacity: 1;
  }
}

/******************************************************************
   
******************************************************************/
@keyframes allowFadeLeft {
  0%{
    transform: translate(0, 0);
    opacity: 1;
  }
  40%{
    opacity: 0;
  }
  49.9%{
    transform: translate(-1em, 0);
    opacity: 0;
  }
  50%{
    transform: translate(1em, 0);
    opacity: 0;
  }
  100%{
    transform: translate(0, 0);
    opacity: 1;
  }
}

/******************************************************************
 
******************************************************************/
@keyframes allowFadeUp {
  0%{
    transform: translate(0, 0);
    opacity: 1;
  }
  40%{
    opacity: 0;
  }
  49.9%{
    transform: translate(0, -1em);
    opacity: 0;
  }
  50%{
    transform: translate(0, 1em);
    opacity: 0;
  }
  100%{
    transform: translate(0, 0);
    opacity: 1;
  }
}
