/******************************************************************

.p-cards-list-02  1  2

******************************************************************/
@use '../global/' as *;

.p-cards-list-02{
  @include mq('tab'){
    display: flex;
    flex-wrap: wrap;
  }
}

// margin-top
.p-cards-list-02__card:not(:first-child){
  margin-top: rem(50);

  @include mq('tab'){
    margin-top: 0;
  }
}

//3margin-top
.p-cards-list-02__card:nth-child(n+3){
  @include mq('tab'){
    margin-top: rem(40);
  }
}

.p-cards-list-02__card{
  @include mq('tab'){
    width: calc(50% - 20px / 2);
  }
}

.p-cards-list-02__card:nth-child(odd){
  @include mq('tab'){
    margin-right: rem(20);
  }
}

