@use './variables' as *;
@use "sass:map"; // 
// map-get()map.get(, )
// DartSass

/***************************************************************



***************************************************************/
@mixin mq($breakpoint: tab) {
	@media #{map.get($breakpoints, $breakpoint)} {
		@content;
	}
}

/***************************************************************

 
animation...
animation: -name -duration -timing -delay -iteration -direction -fill -play;

***************************************************************/
@mixin animation(
	// name
	$name,
	$duration: 1s,
	$timing-function: ease,
	$delay: 0s,
	$iteration-count: 1,
	$direction: normal,
	$fill-mode: forwards
){
	animation: {
		name: $name;
		duration: $duration;
		timing-function: $timing-function;
		delay: $delay;
		iteration-count: $iteration-count;
		direction: $direction;
		fill-mode: $fill-mode;
	}
}